.text90{
  font-size: 5.625em;
  line-height: 120%;
}

.text70{
  font-size: 4.375em;
  line-height: 120%;
}

.text60{
  font-size: 3.75em;
  line-height: 120%;
}

.text42{
  font-size: 2.625em;
  line-height: 120%;
}

.text h3,
.text40{
  font-size: 2.5em;
  line-height: 120%;
}

.text32{
  font-size: 2em;
  line-height: 140%;
}

.text24{
  font-size: 1.5em;
  line-height: 140%;
}

.text22{
  font-size: 1.375em;
  line-height: 120%;
}

.text20{
  font-size: 1.25em;
  line-height: 140%;
}

.text18{
  font-size: 1.125em;
  line-height: 140%;
}

.text li,
.text p,
.m-list li,
.text16{
  font-size: 1em;
  line-height: 140%;
}

.text14{
  font-size: .875em;
  line-height: 140%;
}

.semi-bold-text{
  font-weight: 600;
}

.medium-text{
  font-weight: 500;
}

.primary-text{
  color: $primary;
}

.m-list{
  @include grid100gap(.625em);
  li{
    padding-left: 2em;
    position: relative;
    &:before{
      content: '';
      position: absolute;
      left: 0;
      top: .3125em;
      background-color: $primary;
      width: .75em;
      height: .75em;
    }
  }
}

.text{
  h3{
    @include lastM(0 0 .5em);
  }
  ol,
  p{
    @include lastM(0 0 1em);
  }
  b{
    font-weight: 600;
  }
  ol {
    counter-reset: list;
    li {
      counter-increment: list;
      display: flex;
      &:before {
        content: counter(list) '. ';
        min-width: 1.25em;
        display: inline-block;
      }
    }
  }
  hr{
    display: block;
    height: 1.5em;
    opacity: 0;
  }
}

.lh180{
  line-height: 180%;
}


@media screen and (max-width: 992px){
  .text90{
    font-size: 2.5em;
  }

  .text70{
    font-size: 2.25em;
  }

  .text60,
  .text42{
    font-size: 2em;
  }

  .text h3,
  .text40,
  .text32{
    font-size: 1.5em;
  }

  .text24{
    font-size: 1.25em;
  }


  .text20,
  .text18{
    font-size: 1em;
  }

  .text li,
  .text p,
  .m-list li,
  .text16{
    font-size: .875em;
  }

  .text{
    hr{
      font-size: .875em;
    }
  }
}
