.map{
  height: 45em;
}

.contacts{
  padding-top: 3.75em;
  &__inner{
    position: relative;
  }
}

.contacts-card{
  position: absolute;
  right: 0;
  top: -7em;
  width: 25.9375em;
  padding: 3.125em 1.875em;
  background: #FCF9F8;
  &__top{
    @include grid100gap(.625em);
  }
}

.contacts-item{
  margin-top: 2.5em;
  @include grid100gap(.3125em);
}

.office{
  padding-top: 7.5em;
  background-image: url(../img/office-bg.svg);
  background-size: 115.1875em auto;
  background-position: left 2.44em top -4em;
  background-repeat: no-repeat;
}


@media screen and (max-width: 992px){
  .map{
    height: 20em;
  }

  .contacts{
    padding-top: 1em;
  }

  .contacts-card{
    position: static;
    margin-bottom: 1em;
    width: 100%;
    padding: 1em;
  }

  .contacts-item{
    margin-top: 1em;
  }

  .office{
    padding-top: 3em;
    background-image: unset;
  }
}
