.portfolio{
  padding-bottom: 5em;
  &__items{
    @include grid100gap(5em);
  }
}

.portfolio-item{
  &__top{
    display: flex;
    align-items: center;
  }
  &__badges{
    display: flex;
    align-items: center;
    margin-left: 3.125em;
  }
  &__badge{
    @include center;
    padding: 0 1.875em;
    height: 3.875em;
    background: #FCF9F8;
    margin-right: .625em;
  }
}

.portfolio-slider{
  overflow: hidden;
  margin-top: 1.125em;
  position: relative;
  .swiper{
    &-slide{
      width: 101.25em;
      padding-right: 1.25em;
    }
    &-nav{
      position: absolute;
      right: 13.44em;
      top: 50%;
      transform: translateY(-50%);
      display: flex;
      z-index: 3;
    }
    &-button{
      position: static;
      margin: 0 0 0 .625em;
    }
    &-pagination{
      font-size: 1.875em;
      line-height: 140%;
      width: 4.33em;
      height: 6em;
      background: #FCF9F8;
      bottom: 0;
      left: 3.33em;
      display: flex;
      align-items: center;
      justify-content: center;
      padding-top: 2em;
      &-current{
        font-size: 2.66em;
        transform: translateY(-.1em);
      }
    }
  }
  &__img{
    @include imgCover(40.625%);
  }
}


@media screen and (max-width: 992px){
  .portfolio{
    padding-bottom: 2em;
    &__items{
      gap: 2em;
    }
  }

  .portfolio-item{
    &__top{
      display: block;
    }
    &__badges{
      margin: .25em 0 0;
      flex-wrap: wrap;
    }
    &__badge{
      margin: .5em .625em 0 0;
      height: 2.25em;
      padding: 0 1em;
    }
  }

  .portfolio-slider{
    .swiper{
      &-slide{
        width: 90%;
        padding-right: .625em;
      }
      &-nav,
      &-pagination{
        display: none;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .portfolio-slider{
    &__img{
      padding-bottom: 60%;
    }
  }
}
