.promo{
  padding-bottom: 5em;
  &__items{
    @include grid100gap(1.25em);
  }
}


.promo-card{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  background-size: cover;
  background-position: center;
  min-height: 45em;
  padding: 6.7em;
  color: #fff;
  &:before{
    background: linear-gradient(109deg, rgba(51, 32, 21, 0.90) 16.07%, rgba(51, 32, 21, 0.00) 62.99%);
  }
  &__inner{
    position: relative;
    z-index: 3;
  }
  &__badge{
    position: absolute;
    top: 3.125em;
    right: 3.125em;
    @include center;
    border-radius: 50%;
    background-color: $primary;
    width: 9.375em;
    height: 9.375em;
  }
  &__nav{
    display: flex;
    margin-top: 1.875em;
  }
  &__btn{
    width: 16.9375em;
    max-width: 100%;
    &:after{
      border-color: #fff;
    }
  }
}


@media screen and (max-width: 992px){
  .promo{
    padding-bottom: 2em;
  }

  .promo-card{
    padding: 4em 2.5em;
    min-height: 25em;
    &__badge{
      width: 4em;
      height: 4em;
      top: 1em;
      right: 1em;
    }
    &__nav{
      margin-top: 1.25em;
    }
  }
}


@media screen and (max-width: 576px){
  .promo-card{
    padding: 4em 1em;
    min-height: 20em;
  }
}
