.modal{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: $fullHeight;
    z-index: 100;
    text-align: center;
    overflow-y: auto;
    padding: 1em;
    opacity: 0;
    pointer-events: none;
    @media screen and (min-width: 992px){
        @media screen and (max-width: 1700px){
            font-size: 1.25em;
        }
    }
    &._active{
        opacity: 1;
        pointer-events: unset;
    }
    &::after {
        display: inline-block;
        vertical-align: middle;
        width: 0;
        height: 100%;
        content: '';
    }
    &__bg{
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        height: $fullHeight;
        background: rgba(60, 58, 58, 0.70);
    }
    &__dialog{
        display: inline-block;
        vertical-align: middle;
        text-align: left;
        width: 45.5em;
        padding: 6.25em;
        max-width: 100%;
        position: relative;
        z-index: 2;
        background: #FCF9F8;
    }
    &__close{
        position: absolute;
        top: 1.875em;
        right: 1.875em;
        width: 1.5em;
        background-color: transparent;
        svg{
            width: 100%;
            height: auto;
        }
        &:hover{
            path{
                fill: $primary;
            }
        }
    }
    &__top{
        text-align: center;
    }
}

.modal-form{
    margin-top: 1.875em;
    @include grid100gap(1.875em);
    &__nav{
        padding: 1.25em 0 0 .625em;
    }
    &__btn{
        width: 100%;
    }
}


@media screen and (max-width: 992px){
    .modal{
        &:after{
            display: none;
        }
        &__dialog{
            display: block;
        }
    }
}


@media screen and (max-width: 576px){
    .modal{
        &__dialog{
            padding: 3em 1em;
        }
        &__close{
            top: 1em;
            right: 1em;
        }
    }

    .modal-form{
        margin-top: 1em;
        gap: 1em;
        &__nav{
            padding: .5em 0 0 .625em;
        }
    }
}
