@import "../vars.scss";
.breadcrumbs {
    padding: 1.25em 0 2.5em;
    @include grid100gap(.3125em);
    &__item {
        font-weight: 500;
        position: relative;
        display: inline;
        color: #BEB3AF;
        span{
            font-size: 0.875em;
            line-height: 120%;
            letter-spacing: 0.0175em;
        }
        &:last-child {
            color: $black;
        }
        &:not(:first-child) {
            &:before {
                content: '...';
                flex-shrink: 0;
                margin: 0 .625em 0 .5em;
                font-size: .875em;
            }
        }
    }
}


@media screen and (max-width: 992px){
    .breadcrumbs{
        padding: .625em 0 1.25em;
        &__item{
            span{
                font-size: .75em;
            }
            &:not(:first-child) {
                &:before {
                    font-size: .75em;
                }
            }
        }
    }
}
