.service-item{
  &__top{
    display: flex;
    align-items: center;
  }
  &__types{
    display: flex;
    margin-left: 8.81em;
  }
  &__type{
    @include center;
    margin-right: .625em;
    padding: 0 3.125em;
    height: 4.5em;
    border: 2px solid #FCF9F8;
    img{
      width: 2em;
      flex-shrink: 0;
      margin-right: 1.25em;
    }
  }
}

.project{
  padding: 5em 0;
  &._bg-brown{
    background: #FCF9F8;
    .example-card{
      background-color: #fff;
      &__btn{
        &:not(:hover){
          &:before{
            background-color: #fff;
          }
        }
      }
    }
  }
  &__grid{
    display: grid;
    grid-template-columns: 35em calc(100% - 36.25em);
    gap: 1.25em;
    margin-top: .56em;
  }
  &__left{
    padding-top: 2em;
  }
  &__bottom{
    margin-top: 3.125em;
    display: flex;
    align-items: center;
  }
  &__btn{
    margin-left: 4.625em;
  }
}

.examples{
  margin-top: 5em;
  &:first-child{
    margin-top: 0;
  }
  &__grid{
    margin-top: 1.25em;
  }
}

.example-card{
  background: #FCF9F8;
  padding: 6.25em 3.125em;
  display: flex;
  flex-direction: column;
  align-items: center;
  &__icon{
    height: 6.25em;
    img{
      height: 100%;
      display: block;
    }
  }
  &__btn{
    margin-top: 3.125em;
    &:not(:hover){
      &:before{
        border: 1px solid $primary;
        background-color: #FCF9F8;
      }
    }
    img{
      position: relative;
      z-index: 2;
      width: 1.5em;
      margin-left: .625em;
    }
  }
}

.structure{
  padding: 14.38em 0;
  margin-bottom: 2.5em;
  background-size: cover;
  background-position: center;
  color: #fff;
  &:before{
    background: linear-gradient(0deg, rgba(51, 32, 21, 0.80) 0%, rgba(51, 32, 21, 0.80) 100%);
  }
  &__inner{
    position: relative;
    z-index: 2;
    max-width: 25.9375em;
  }
  &__icon{
    margin-bottom: 1.25em;
    height: 6.25em;
    width: auto;
    display: block;
  }
}



@media screen and (max-width: 992px){
  .service-item{
    &__top{
      display: block;
    }
    &__types{
      margin: .5em 0 0;
      flex-wrap: wrap;
    }
    &__type{
      height: 2.5em;
      padding: 0 1em;
      margin: .5em .5em 0 0;
      img{
        width: 1.25em;
        margin-right: .5em;
      }
      .text24{
        font-size: 1em;
      }
    }
  }

  .project{
    padding: 2em 0;
    &__grid{
      grid-template-columns: 100%;
      gap: 1em;
      margin-top: 1em;
    }
    &__left{
      padding-top: 0;
    }
    &__bottom{
      margin-top: 2em;
    }
  }

  .examples{
    margin-top: 2em;
    &__grid{
      margin-top: 1em;
    }
  }

  .example-card{
    padding: 3em 1.25em;
    &__icon{
      height: 4em;
    }
    &__btn{
      margin-top: 1.5em;
    }
  }

  .structure{
    padding: 2em 0;
    margin-bottom: 1em;
    &__icon{
      height: 4em;
    }
  }
}


@media screen and (max-width: 576px){
  .project{
    &__bottom{
      flex-direction: column;
      align-items: flex-start;
    }
    &__btn{
      margin: 2em 0 0;
    }
  }
}
