@import 'vars';
@import 'components/_text.scss';
@import 'components/_buttons.scss';
@import "components/_inputs.scss";
.grid-column2,
.grid-column3,
.grid-column4{
  display: grid;
  gap: 1.25em;
}

.grid-column2{
  grid-template-columns: repeat(2,calc(50% - .625em));
}

.grid-column3{
  grid-template-columns: repeat(3,calc(100% / 3 - 2.5em / 3));
}

.grid-column4{
  grid-template-columns: repeat(4,calc(25% - 3.75em / 4));
}

.grid-column6{
  display: grid;
  gap: .3125em;
  grid-template-columns: repeat(6,calc(100% / 6 - 1.5625em / 6));
}

._backdrop{
  position: relative;
  &:before{
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 2;
    background: linear-gradient(0deg, rgba(51, 32, 21, 0.40) 0%, rgba(51, 32, 21, 0.40) 100%);
  }
}

.section{
  padding: 3.75em 0;
  &__top{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 3.125em;
  }
  &__title{
    position: relative;
    .text60{
      position: relative;
      z-index: 2;
    }
    .round-letters{
      position: absolute;
      right: -3em;
      top: 50%;
      transform: translateY(-50%);
    }
  }
  &__nav{
    .m-btn{
      padding: 0 1.875em;
      height: 2.75em;
    }
  }
}

.section2{
  margin: 3.75em 0;
  padding: 7.5em 0;
}

.round-letters{
  width: 7.1em;
  img{
    animation-name: rotation;
    animation-duration: 14s;
    animation-iteration-count: infinite;
    animation-timing-function: linear;
    width: 100%;
    display: block;
  }
}

@keyframes rotation {
  0% {
    transform:rotate(0deg);
  }
  100% {
    transform:rotate(360deg);
  }
}

.page-block{
  padding-bottom: 2.5em;
}

.swiper-button{
  width: 5.625em;
  height: 5.625em;
  @include center;
  svg{
    width: 3.125em;
    height: auto;
  }
}

.play-btn{
  @include centerAbsolute;
  @include center;
  z-index: 2;
  width: 6.875em;
  height: 6.875em;
  svg{
    width: 3.125em;
    height: auto;
  }
}


@media screen and (max-width: 992px){
  .grid-column2{
    grid-template-columns: 100%;
  }

  .grid-column3{
    grid-template-columns: repeat(2,calc(50% - .3125em));
    gap: .625em;
  }

  .grid-column4{
    grid-template-columns: repeat(2,calc(50% - .3125em));
    gap: .625em;
  }

  .grid-column6{
    grid-template-columns: repeat(4,calc(25% - .9375em / 4));
  }

  .section{
    padding: 2em 0;
    &__top{
      margin-bottom: 1em;
    }
    &__title{
      .round-letters{
        right: -1em;
      }
    }
  }

  .section2{
    margin: 2em 0;
    padding: 2em 0;
  }

  .round-letters{
    width: 3em;
  }

  .page-block{
    padding-bottom: 1em;
  }

  .play-btn,
  .swiper-button{
    width: 2.625em;
    height: 2.625em;
    svg{
      width: 1.25em;
    }
  }

}


@media screen and (max-width: 576px){
  .grid-column3,
  .grid-column4{
    grid-template-columns: 100%;
  }

  .grid-column6{
    grid-template-columns: repeat(2,calc(50% - .3125em / 2));
  }

  .section{
    &__inner{
      position: relative;
      padding-bottom: 5em;
    }
    &__nav{
      position: absolute;
      bottom: 0;
      left: 0;
      width: 100%;
      display: flex;
      justify-content: center;
      .m-btn{
        width: 100%;
      }
    }
  }
}
