.partners{
  background: #FCF9F8;
}

.partner-card{
  padding: 2em;
  height: 8.875em;
  background-color: #fff;
  position: relative;
  &__img{
    width: 100%;
    height: 100%;
    @include center;
    img{
      max-width: 100%;
      max-height: 100%;
      display: block;
      flex-grow: 1;
    }
  }
  &__tag{
    position: absolute;
    background-color: #fff;
    bottom: .625em;
    left: 50%;
    transform: translateX(-50%);
    height: 2.4375em;
    padding: 0 1.25em;
    @include center;
    color: #AA9088;
    span{
      font-size: 1em;
    }
  }
}

.video-reviews{
  &._pb0{
    padding-bottom: 0;
  }
}

.gallery-slider{
  position: relative;
  overflow: hidden;
  &__img{
    display: block;
    @include imgCover(37.5%);
    img{
      display: block;
    }
  }
  .swiper{
    &-button{
      &-prev{
        left: 6.25em;
      }
      &-next{
        right: 6.25em;
      }
    }
  }
}

.main{
  overflow: hidden;
  position: relative;
  .swiper{
    &-button{
      background-color: transparent;
      border: 1px solid $primary;
      width: 4.5em;
      height: 4.5em;
      &:hover{
        background-color: $primary;
      }
      svg{
        width: 2em;
      }
      &-prev{
        left: 6.25em;
      }
      &-next{
        right: 6.25em;
      }
    }
    &-pagination{
      z-index: 2;
      left: 6.25em;
      bottom: 3.125em;
      width: unset;
      display: flex;
      align-items: center;
      &-bullet{
        background: linear-gradient(0deg, #939393 0%, #939393 100%), linear-gradient(0deg, #939393 0%, #939393 100%), #939393;
        opacity: 1;
        width: 0.75em;
        height: 0.75em;
        margin: 0 .625em 0 0 !important;
        border-radius: 0;
        &-active{
          background: $primary;
        }
      }
    }
  }
}

.main-slide{
  background-size: cover;
  background-position: center;
  &:before{
    background: linear-gradient(0deg, rgba(51, 32, 21, 0.80) 0%, rgba(51, 32, 21, 0.80) 100%);
  }
  &__inner{
    padding: 19em 0;
    max-width: 90.25em;
    margin: 0 auto;
    text-align: center;
    position: relative;
    z-index: 2;
  }
  &__title{
    font-size: 6.25em;
    line-height: 120%;
    color: #fff;
  }
  .text60{
    margin-top: .18em;
  }
  &__nav{
    margin-top: 3.75em;
    display: flex;
    justify-content: center;
  }
  &__btn{
    width: 22.9375em;
    max-width: 100%;
    &:after{
      border-color: #fff;
    }
  }
}

.main-services{
  margin-top: 0;
  background: #FCF9F8;
  @media screen and (min-width: 993px){
    .services__grid{
      grid-template-columns: repeat(5,20%);
    }
  }
}

.reviews{
  overflow: hidden;
}

.reviews-slider{
  width: 81.5625em;
  max-width: 100%;
  position: relative;
  .swiper{
    &-slide{
      padding-right: 1.25em;
      transition: all .3s;
      opacity: .3;
      &-active{
        opacity: 1;
      }
    }
    &-nav{
      position: absolute;
      right: -1.75em;
      top: 50%;
      transform: translateY(-50%);
      z-index: 3;
    }
    &-button{
      position: static;
      margin: 0;
      width: 4.5em;
      height: 4.5em;
      &-next{
        margin-top: .625em;
      }
      svg{
        width: 2em;
      }
    }
  }
}

.review-card{
  display: grid;
  grid-template-columns: 35em calc(100% - 35em);
  background: #FCF9F8;
  position: relative;
  &__img{
    display: block;
    min-height: 100%;
    @include imgCover(71.4%);
  }
  &__content{
    padding: 3.75em 3.125em;
  }
  .text{
    max-width: 34em;
    width: 100%;
  }
  &__by{
    margin-top: 3.125em;
    color: #BEB3AF;
    font-weight: 600;
  }
  &__btn{
    position: absolute;
    bottom: 1.25em;
    left: 1.25em;
    z-index: 3;
    height: 3.25em;
    padding: 0 1.25em;
    span{
      font-size: 1em;
    }
    img{
      width: 2em;
      flex-shrink: 0;
      margin-right: 1.25em;
    }
  }
}

.place{
  position: relative;
  z-index: 2;
  background-image: url(../img/place-bg.png);
  background-position: center bottom;
  background-repeat: no-repeat;
  background-size: calc(100% - 4.8em);
  &__grid{
    display: grid;
    grid-template-columns: 45.4375em calc(100% - 45.4375em - 12.5em);
    gap: 12.5em;
  }
  &__content{
    padding: 7.12em 0;
    min-height: 45em;
  }
  &__desc{
    margin-top: 2.5em;
    max-width: 35em;
  }
  &__nav{
    display: flex;
    padding: 3.125em 0 0 .625em;
  }
  &__btn{
    padding: 0 3.125em;
  }
  &__images{
    position: relative;
  }
  &__img{
    display: block;
    width: 100%;
    max-width: 43.625em;
    margin-top: -6.25em;
    position: relative;
    z-index: 2;
  }
  &__img2{
    position: absolute;
    left: -8em;
    bottom: 3em;
    width: 31.625em;

  }
}


@media screen and (max-width: 992px){
  .partner-card {
    padding: 1em;
    height: 5em;
    &__tag{
      bottom: .3125em;
      height: 1.875em;
      padding: 0 1em;
      span{
        font-size: .875em;
      }
    }
  }

  .gallery-slider{
    &__img{
      padding-bottom: 50%;
    }
    .swiper{
      &-slide{
        width: 90%;
        padding-right: .5em;
      }
      &-button{
        display: none;
      }
    }
  }

  .main{
    .swiper{
      &-pagination,
      &-button{
        display: none;
      }
    }
  }

  .main-slide{
    &__inner{
      padding: 6em 0 2em;
      max-width: 35em;
    }
    &__title{
      font-size: 2.25em;
    }
    &__nav{
      margin-top: 1.5em;
    }
  }

  .reviews-slider{
    .swiper{
      &-slide{
        padding-right: .625em;
      }
      &-nav{
        display: none;
      }
    }
  }

  .review-card{
    grid-template-columns: 20em calc(100% - 20em);
    &__content{
      padding: .5em 1em;
    }
    &__by{
      margin-top: 1em;
    }
  }

  .place{
    padding: 2em 0;
    &__grid{
      grid-template-columns: 100%;
      gap: 1em;
    }
    &__content{
      padding: 0;
      min-height: unset;
    }
    &__images{
      order: -1;
    }
    &__img{
      margin: 0;
    }
    &__img2{
      display: none;
    }
    &__desc{
      margin-top: 1em;
    }
    &__nav{
      padding-top: 1.5em;
    }
    &__btn{
      padding: 0 1.25em;
    }
  }
}


@media screen and (max-width: 576px){
  .main-slide{
    &__title{
      font-size: 2em;
    }
    .text60{
      font-size: 1.5em;
    }
  }

  .review-card{
    grid-template-columns: 100%;
    &__btn{
      width: 2.5em;
      height: 2.5em;
      padding: 0;
      top: .5em;
      left: .5em;
      bottom: unset;
      img{
        width: 1.5em;
        margin: 0;
      }
      span{
        display: none;
      }
    }
  }

  .place{
    &__btn{
      width: 100%;
    }
  }
}
