.doc{
  padding-bottom: 5em;
}

.certificate-card{
  padding: 3.125em 4.22em;
  background: #FCF9F8;
  &__img{
    display: block;
    img{
      width: 100%;
      display: block;
    }
  }
  &__title{
    @include center;
    text-align: center;
    padding: .625em 1.25em;
    background: #FFF;
    min-height: 3.0625em;
    margin-top: .625em;
  }
}

.privacy{
  margin-top: 5em;
  display: grid;
  grid-template-columns: calc(25% - 3.75em / 4) calc(75% - 3.75em / 4);
  gap: 1.25em;
  align-items: center;
  .text{
    margin-top: 2.5em;
    max-width: 42.125em;
  }
}


@media screen and (max-width: 992px){
  .doc{
    padding-bottom: 2em;
  }

  .certificate-card{
    padding: 1.25em 1em;
  }

  .privacy{
    margin-top: 2em;
    grid-template-columns: 100%;
    text-align: center;
    gap: 1em;
    .example-card{
      width: 20em;
      max-width: 100%;
      margin: 0 auto;
    }
    .text{
      margin-top: 1em;
    }
  }
}


@media screen and (max-width: 576px){
  .privacy{
    text-align: left;
    .example-card{
      width: 100%;
    }
  }
}
