.news{
  &__nav{
    display: flex;
    justify-content: center;
    margin-top: 2.5em;
  }
}

.news-card{
  background: #FCF9F8;
  position: relative;
  &__img{
    @include imgCover(70%);
  }
  &__content{
    padding: 1.875em 3.125em 3.125em;
  }
  &__text{
    @include grid100gap(1.25em);
    .text16{
      @include truncate(4);
    }
  }
  &__nav{
    display: flex;
    margin-top: 2.19em;
    padding-left: .3125em;
  }
  @media screen and (min-width: 577px){
    &._big{
      grid-column: span 2;
      color: #FFF;
      .news-card{
        &__img{
          &:before{
            background: linear-gradient(0deg, rgba(51, 32, 21, 0.80) 0%, rgba(51, 32, 21, 0.80) 100%);
          }
        }
        &__content{
          padding: 3.44em 3.125em;
          position: absolute;
          bottom: 0;
          left: 0;
          z-index: 3;
        }
        &__btn{
          &:after{
            border-color: #fff;
          }
          &:not(:hover){
            color: #FFF;
            &:before{
              background-color: transparent;
            }
          }
        }
      }
    }
  }
}


@media screen and (max-width: 992px){
  .news{
    &__nav{
      margin-top: 1em;
    }
  }

  .news-card{
    &__content{
      padding: 1.25em;
    }
    &__text{
      gap: .75em;
    }
    &__nav{
      margin-top: 1em;
    }
    &._big{
      @media screen and (min-width: 577px){
        .news-card{
          &__content{
            padding: 1.25em;
          }
        }
      }
    }
  }
}


