.header{
  padding: 1.25em 0;
  &__inner{
    display: flex;
    align-items: center;
  }
  &__logo{
    display: block;
    width: 6.5625em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__right{
    margin-left: auto;
    display: flex;
    align-items: center;
  }
  &__btn{
    margin-left: 5.19em;
  }
  &._open{
    //padding-right: var(--scrollWidth);
    .menu{
      transform: translateX(0);
      &__bg{
        display: block;
      }
    }
    .burger{
      ._view1{
        opacity: 0;
      }
      ._view2{
        opacity: 1;
      }
    }
  }
  &--index{
    color: #fff;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 10;
    .header{
      &__btn{
        &:after{
          border-color: #fff;
        }
      }
    }
    .socials{
      &__item{
        path{
          fill: #fff;
        }
      }
    }
  }
}

.burger{
  margin-right: 3.1875em;
  width: 6.25em;
  height: 6.25em;
  flex-shrink: 0;
  position: relative;
  z-index: 21;
  img{
    @include centerAbsolute;
    width: 2.25em;
    transition: inherit;
  }
  ._view2{
    opacity: 0;
  }
}

.header-contacts{
  &__status,
  &__wrap{
    display: flex;
    align-items: center;
  }
  &__wrap{
    margin-top: .3125em;
  }
  &__status{
    margin-right: .625em;
    &:before{
      content: '';
      width: 0.75em;
      height: 0.75em;
      flex-shrink: 0;
      margin-right: .75em;
      background-color: $primary;
    }
  }
}

.socials{
  display: flex;
  align-items: center;
  &__item{
    width: 1.5em;
    flex-shrink: 0;
    @include center;
    @include lastM(0 .625em 0 0);
    svg{
      width: 100%;
      height: auto;
    }
    &:hover{
      path{
        fill: $primary;
      }
    }
  }
}

.menu{
  position: fixed;
  top: 0;
  left: 0;
  max-width: 100%;
  width: 41.25em;
  height: $fullHeight;
  z-index: 11;
  background: $black;
  padding: 12em 6.25em 1em;
  overflow-y: auto;
  transform: translateX(-100%);
  transition: all .3s;
  &__bg{
    opacity: 0.8;
    background: #322922;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: $fullHeight;
    z-index: 10;
    display: none;
  }
  &__links{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
  &__link{
    font-weight: 500;
    color: #FFF;
    @include lastM(0 0 1em);
    span{
      font-size: 2em;
      line-height: 140%;
    }
    &._active,
    &:hover{
      color: $primary;
    }
  }
}


@media screen and (max-width: 992px){
  .header{
    padding: .75em 0;
    &__logo{
      width: 4em;
    }
    &__btn{
      margin-left: 2em;
    }
  }

  .burger{
    width: 3em;
    height: 3em;
    margin-right: 1em;
    img{
      width: 1.5em;
    }
  }

  .socials{
    &__item{
      width: 1em;
    }
  }

  .menu{
    width: 20em;
    padding: 6em 1.25em 1em;
    &__link{
      span{
        font-size: 1.25em;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .header{
    &__btn{
      display: none;
    }
  }
}
