@import "../vars";
.m-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0;
    &:focus {
        box-shadow: none;
    }
}

.m-btn-primary{
    background-color: $primary;
    &:hover{
        background-color: #F5CF98;
    }
    &:active{
        background-color: #CEA468;
    }
}

.m-btn-default{
    height: 4.1875em;
    padding: 0 3.75em;
    position: relative;
    font-weight: 500;
    color: $black;
    &:hover{
        color: $black;
        &:before{
            background-color: #F5CF98;
        }
    }
    &:active{
        color: $black;
        &:before{
            background-color: #CEA468;
        }
    }
    &:after,
    &:before{
        content: '';
        position: absolute;
        width: 100%;
        height: 100%;
        transition: inherit;
        left: 0;
        top: 0;
    }
    &:before{
        background-color: $primary;
        z-index: 2;
    }
    &:after{
        transform: translate(-.625em,-.625em);
        border: 2px solid $black;
    }
    span{
        font-size: 1.125em;
        line-height: 150%;
        letter-spacing: 0.0225em;
        position: relative;
        z-index: 3;
    }
    &._type2{
        height: 3.125em;
        padding: 0 3.125em;
        &:after{
            transform: translate(-.3125em,-.3125em);
        }
        &:not(:hover){
            &:before{
                border: 2px solid $primary;
                background-color: #FCF9F8;
            }
        }
        span{
            font-size: 0.875em;
        }
    }
    img{
        width: 1.5em;
        position: relative;
        z-index: 2;
        margin-left: .625em;
    }
    &._outline{
        &:not(:hover){
            &:before{
                border: 2px solid $primary;
                background-color: #FCF9F8;
            }
        }
    }
}

.m-btn-gray{
    color: $black;
    background-color: #FCF9F8;
    padding: 0 3.125em;
    height: 4em;
    font-weight: 500;
    span{
        font-size: .875em;
        line-height: 150%;
        letter-spacing: 0.0175em;
    }
    svg{
        width: 1.5em;
        height: auto;
        flex-shrink: 0;
        margin-left: .625em;
    }
    &:hover{
        background-color: $black;
        color: #fff;
        path{
            fill: #fff;
        }
    }
}


@media screen and (max-width: 992px){
    .m-btn-default{
        height: 3em;
        padding: 0 1.25em;
        span{
            font-size: 1em;
        }
        &:after{
            transform: translate(-.375em,-.375em);
        }
    }

    .m-btn-gray{
        height: 3em;
        padding: 0 1.25em;
        svg{
            width: 1.25em;
        }
    }
}
