.services{
  padding-bottom: 2.5em;
  &__grid{
    margin-top: 3.125em;
    display: grid;
    grid-template-columns: repeat(4,25%);
  }
}

.service-card{
  border-right: 1px solid #EBE2DF;
  padding: 3.125em;
  &__icon{
    height: 5em;
    margin-bottom: 2.875em;
    position: relative;
    img{
      height: 100%;
      display: block;
    }
    &:after{
      content: '';
      position: absolute;
      top: 100%;
      left: 0;
      margin-top: 1.375em;
      width: 3.93294em;
      height: 0.125em;
      background-color: $primary;
    }
  }
  &__title{
    min-height: 2.4em;
  }
  &__nav{
    display: flex;
    padding: 1.375em 0 0 .3125em;
  }
  &__btn{
    height: 3.125em;
    padding: 0 3.125em;
    span{
      font-size: .875em;
    }
    &:after{
      transform: translate(-.3125em,-.3125em);
    }
    &:not(:hover){
      &:before{
        border: 2px solid $primary;
        background-color: #fff;
      }
    }
    img{
      width: 1.5em;
      position: relative;
      z-index: 2;
      margin-left: .265em;
    }
  }
}

.construction{
  background: #FCF9F8;
  padding: 7.5em 0;
  margin-bottom: 3.75em;
  @media screen and (min-width: 993px){
    .services__grid{
      grid-template-columns: repeat(3,calc(100% / 3));
    }
    .service-card{
      border-top: 1px solid #EBE2DF;
      &:nth-child(1),
      &:nth-child(2),
      &:nth-child(3){
        border-top: none;
      }
    }
  }
}

.advantages{
  &__inner{
    position: relative;
  }
  &__grid{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .625em));
    gap: 1.25em;
  }
}

.advantage-card{
  min-height: 31.25em;
  background: #FCF9F8;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  padding: 3.125em;
  @media screen and (min-width: 993px){
    &:nth-child(even){
      padding-left: 25em;
    }
  }
  &__icon{
    height: 5em;
    img{
      width: auto;
      height: 100%;
      display: block;
    }
  }
  &__info{
    margin-top: 1.25em;
    @include grid100gap(.625em);
    max-width: 24.5em;
  }
}

.advantage-item{
  @include centerAbsolute;
  z-index: 2;
  width: 43.75em;
  color: #fff;
  &:before,
  &:after{
    content: '';
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    width: 0.0625em;
    height: 25em;
    background-color: $primary;
    z-index: 3;
  }
  &:before{
    top: -10.75em;
  }
  &:after{
    bottom: -10.75em;
  }
  &__img{
    @include imgCover(100%);
    &:before{
      background: linear-gradient(0deg, rgba(51, 32, 21, 0.80) 0%, rgba(51, 32, 21, 0.80) 100%);
    }
  }
  &__title{
    @include centerAbsolute;
    padding: 2em;
    width: 100%;
    z-index: 2;
    text-align: center;
  }
}

.consult{
  padding: 7.5em 0;
  background-color: #FCF9F8;
  background-image: url(../img/consult-bg.svg);
  background-size: calc(100% - 2.37em) auto;
  background-repeat: no-repeat;
  background-position: left 2.37em top 4.56em;
  &__inner{
    width: 62.5em;
    max-width: 100%;
    margin: 0 auto;
  }
  .text60{
    text-align: center;
    max-width: 95%;
    margin: 0 auto;
  }
}

.consult-form{
  margin-top: 2.875em;
  &__fields{
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3 -  1.25em));
    gap: 1.875em;
  }
  ._full{
    grid-column: 1/-1;
  }
  &__nav{
    display: flex;
    justify-content: center;
    margin-top: 2.5em;
  }
  &__btn{
    width: 16.9375em;
    max-width: 100%;
  }
}


@media screen and (max-width: 992px){
  .services{
    padding-bottom: 1em;
    &__grid{
      grid-template-columns: 50% 50%;
      margin-top: 1em;
    }
  }

  .service-card{
    padding: 1.25em;
    border-top: 1px solid #EBE2DF;
    &:nth-child(1){
      border-top: none;
    }
    @media screen and (min-width: 577px){
      &:nth-child(2){
        border-top: none;
      }
      &:nth-child(even){
        border-right: none;
      }
    }
    &__icon{
      height: 4em;
    }
  }

  .construction{
    padding: 2em 0;
    margin-bottom: 2em;
  }

  .advantages{
    &__grid{
      grid-template-columns: repeat(2,calc(50% - .3125em));
      gap: .625em;
    }
  }

  .advantage-item{
    width: 100%;
    position: relative;
    left: 0;
    top: 0;
    transform: unset;
    margin-bottom: .625em;
    &:before,
    &:after{
      display: none;
    }
    &__img{
      padding-bottom: 70%;
    }
    &__title{
      padding: 1em;
    }
  }

  .advantage-card{
    padding: 1.25em;
    min-height: unset;
    &__icon{
      height: 4em;
    }
  }

  .consult{
    padding: 2em 0;
    background-image: unset;
  }

  .consult-form{
    margin-top: 1em;
    &__fields{
      grid-template-columns: repeat(3,calc(100% / 3 - 1.25em / 3));
      gap: .625em;
    }
  }
}


@media screen and (max-width: 576px){
  .services{
    &__grid{
      grid-template-columns: 100%;
    }
  }

  .service-card{
    border-right: none;
    padding-left: 0;
    padding-right: 0;
    text-align: center;
    &__icon{
      margin: 0 auto 1.5em;
      display: flex;
      justify-content: center;
      &:after{
        margin-top: .75em;
        left: 50%;
        transform: translateX(-50%);
      }
    }
    &__title{
      min-height: unset;
      br{
        display: none;
      }
    }
    &__nav{
      padding-top: 1em;
    }
    &__btn{
      width: 100%;
      height: 2.5em;
    }
  }

  .advantages{
    &__grid{
      grid-template-columns: 100%;
    }
  }

  .consult-form{
    &__fields{
      grid-template-columns: 100%;
    }
    &__nav{
      margin-top: 1em;
    }
    &__btn{
      width: 100%;
    }
  }
}
