.project-card,
.project-card2{
  display: block;
  &__img{
    @include imgCover(89.4%);
  }
}

.project-card{
  &__img{
    margin-bottom: 1.25em;
  }
  &__nav{
    display: flex;
    padding: 1.56em 0 0 .3125em;
  }
  &__btn{
    height: 3.125em;
    padding: 0 3.125em;
    &:after{
      transform: translate(-.3125em,-.3125em);
    }
    img{
      position: relative;
      z-index: 2;
      width: 1.5em;
      margin-left: .625em;
    }
  }
}

.project-collection{
  background: #FCF9F8;
  padding: 2.5em 0;
  &__grid{
    row-gap: 2.5em;
  }
}

.project-collection2{
  padding: 5em 0;
}

.project-card2{
  position: relative;
  &__img{
    padding-bottom: 82%;
  }
  &__content{
    position: absolute;
    bottom: 0;
    left: 0;
    width: 31.25em;
    max-width: 80%;
    z-index: 2;
    background: #FCF9F8;
    padding: 1.375em 1.875em;
  }
  &__badge{
    color: $black;
    position: absolute;
    top: 1.875em;
    left: 1.875em;
    height: 3.4375em;
    padding: 0 2.5em;
    background: #FCF9F8;
    font-weight: 600;
    @include center;
  }
}


@media screen and (max-width: 992px){
  .project-card{
    &__img{
      margin-bottom: .5em;
    }
    &__nav{
      padding-top: 1em;
    }
    &__btn{
      width: 100%;
      padding: 0;
    }
  }

  .project-collection{
    padding: 1em 0;
    &__grid{
      row-gap: 1.5em;
    }
  }

  .project-collection2{
    padding: 2em 0;
  }

  .project-card2{
    &__badge{
      top: .5em;
      left: .5em;
      padding: 0 1.25em;
      height: 2em;
    }
    &__content{
      padding: .75em .5em;
    }
  }
}
