.footer{
  background-color: $black;
  color: #fff;
  padding: 1.875em 0;
  &__inner{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__logo{
    display: block;
    width: 9.4375em;
    img{
      width: 100%;
      display: block;
    }
  }
}


@media screen and (max-width: 992px){
  .footer{
    padding: 1em 0;
    &__logo{
      width: 4em;
    }
  }
}
