.fg {
  position: relative;
  margin: 0;
  textarea,
  input {
    border: none;
    border-bottom: 1px solid $black;
    padding: 0 1.875em;
    height: 3.875em;
    width: 100%;
    font-size: 1em;
    line-height: 140%;
    background-color: transparent;
    color: $black;
    &::placeholder {
      color: #AEA09B;
    }
    &:focus {
      border-color: $primary;
    }
  }
  textarea{
    padding-top: 1.25em;
    padding-bottom: 1.25em;
    height: 5em;
    resize: none;
  }
}

.file-input{
  position: relative;
  input{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 2;
    cursor: pointer;
    opacity: 0;
  }

  &__inner{
    @include center;
    height: 8.75em;
    border: 1px dashed $black;
    padding: 1em;
    img{
      width: 2.5em;
      margin-right: 1.19em;
    }
  }
}

.check {
  position: relative;
  input {
    position: absolute;
    opacity: 0;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    z-index: 2;
    cursor: pointer;
    &:checked+label {
      &::before {
        // background-color: $green;
        border: 3px solid #fff;
      }
    }
    &:disabled+label {
      color: #AAAAAA;
      pointer-events: none;
    }
  }
  label {
    padding-left: 40px;
    position: relative;
    min-height: 20px;
    font-size: 14px;
    line-height: 20px;
    cursor: pointer;
    &:before {
      content: '';
      position: absolute;
      z-index: 1;
      border-radius: 2px;
      background: rgba(255, 255, 255, 0.01);
      border: 1px solid rgba(255, 255, 255, 0.45);
      width: 20px;
      height: 20px;
      left: 0;
      top: 50%;
      transform: translateY(-50%);
      transition: all .100s;
    }
  }
}


@media screen and (max-width: 992px){
  .fg{
    textarea,
    input{
      padding-left: .5em;
    }
  }

  .file-input{
    &__inner{
      height: 6em;
      img{
        width: 1.25em;
        margin-right: .5em;
      }
    }
  }
}
