.articles{
  padding-bottom: 5em;
  &__items{
    @include grid100gap(1.25em);
  }
}

.article-card{
  background: #FCF9F8;
  display: grid;
  grid-template-columns: 44.0625em calc(100% - 44.0625em);
  &__img{
    min-height: 100%;
    @include imgCover(56.7%);
  }
  &__content{
    padding: 3.27em 3.125em;
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
  .text16{
    margin: 1.75em 0;
    @include truncate(4);
    min-height: 5.6em;
  }
  &__nav{
    display: flex;
    padding-left: .3125em;
  }
  &__btn{
    height: 3.125em;
    padding: 0 3.125em;
    &:after{
      transform: translate(-.3125em,-.3125em);
    }
  }
}


@media screen and (max-width: 992px){
  .articles{
    padding-bottom: 2em;
  }

  .article-card{
    grid-template-columns: 100%;
    &__content{
      padding: 1.25em;
    }
    .text16{
      margin: 1.25em 0;
    }
  }
}
