.gallery{
  padding-bottom: 5em;
  &__grid{
    display: grid;
    grid-template-columns: repeat(6,calc(100% / 6 - 6.25em / 6));
    gap: 1.25em;
    grid-auto-flow: dense;
  }
}

.gallery-item{
  position: relative;
  &:hover{
    &:before{
      opacity: 1;
    }
  }
  &:before{
    z-index: 3;
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-image: url(../img/plus.svg), linear-gradient(0deg, rgba(51, 32, 21, 0.80) 0%, rgba(51, 32, 21, 0.80) 100%);
    background-size: 5em auto,cover;
    background-position: center;
    background-repeat: no-repeat;
    transition: all .3s;
    pointer-events: none;
    opacity: 0;
  }
  &__img{
    min-height: 100%;
    display: block;
    @include imgCover(88.9%);
  }
  @media screen and (min-width: 993px){
    &._big{
      grid-column: span 2;
      grid-row: span 2;
    }
    &._big2{
      grid-column: span 3;
      grid-row: span 2;
      .gallery-item__img{
        padding-bottom: 58%;
      }
    }
  }
}


@media screen and (max-width: 992px){
  .gallery{
    padding-bottom: 2em;
    &__grid{
      grid-template-columns: repeat(3,calc(100% / 3 - 1.25em / 3));
      gap: .625em;
    }
  }
}


@media screen and (max-width: 576px){
  .gallery{
    .text60{
      font-size: 1.75em;
    }
    &__grid{
      grid-template-columns: repeat(2,calc(50% - .3125em));
    }
  }
}
