.career{
  padding-bottom: 5em;
  &__grid{
    display: grid;
    grid-template-columns: calc(100% - 52.6em) 51.6em;
    gap: 1em;
    align-items: center;
  }
  &__content{
    max-width: 53.125em;
  }
  &__img{
    img{
      width: 100%;
      display: block;
    }
  }
  .text{
    b{
      font-weight: 500;
    }
  }
}

.career-form{
  margin-top: .625em;
  &__fields{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .9em));
    gap: 1.25em 1.81em;
  }
  &__nav{
    display: flex;
    padding: 1.58em 0 0 .3125em;
  }
  &__btn{
    &:after{
      transform: translate(-.3125em,-.3125em);
    }
  }
  ._full{
    grid-column: 1/-1;
  }
}

.vacancies{
  padding: 5em 0;
  background: #FCF9F8;
  &__grid{
    margin-top: 2.5em;
  }
}

.vacancy-card{
  &__img{
    @include imgCover(96.3%);
  }
  &__content{
    margin-top: 1.25em;
    @include grid100gap(1.25em);
  }
}


@media screen and (max-width: 992px){
  .career{
    padding-bottom: 2em;
    &__grid{
      grid-template-columns: 100%;
    }
    &__img{
      order: -1;
    }
  }

  .vacancies{
    padding: 2em 0;
    &__grid{
      margin-top: 1.25em;
      row-gap: 1.25em;
    }
  }

  .vacancy-card{
    &__content{
      margin-top: .625em;
      gap: .625em;
    }
  }
}


@media screen and (max-width: 576px){
  .career-form{
    &__fields{
      grid-template-columns: 100%;
    }
    &__btn{
      width: 100%;
    }
  }
}
